import styled from "styled-components";
import VideoComponent from "./VideoComponent";
import { useTranslation } from "react-i18next";

const Container = styled.div`
  padding: 200px 30px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  gap: 150px;
  @media screen and (min-width: 1280px) {
    max-width: 1200px;
    padding: 300px 30px;
  }
  @media screen and (max-width: 768px) {
    gap: 80px;
    padding: 120px 30px;
  }
  @media screen and (max-width: 600px) {
    padding: 80px 0;
  }
`;

function VideoCont() {
  const { t } = useTranslation();

  return (
    <Container>
      <VideoComponent
        key={t("explore_video_ko")}
        title={t("   ")}
        description={t(
          "AI  3D      .\n  :  0.2mm(20~50cm )"
        )}
        videoSource={`https://rloo8.github.io/RebuilderAi/image/${t(
          "explore_video_ko"
        )}.mp4`}
      />
      <VideoComponent
        title={t("  ")}
        description={t(
          "  ,     \n3D    . \n       ."
        )}
        videoSource="https://rloo8.github.io/RebuilderAi/image/explore_video2.mp4"
      />
      <VideoComponent
        title={t("3D ")}
        description={t(
          "       \n      . \nAI     3D    ."
        )}
        videoSource="https://rloo8.github.io/RebuilderAi/image/explore_video3.mp4"
      />
    </Container>
  );
}

export default VideoCont;
